<script>
import axios from 'axios';
import autophp from '@/plugins/auto';
let sdb = new autophp('ci');
var maps;
var watchId;
export default {
  data() {
    return {
      isInside:false,
      lat:'',
      long:'',
      users:false
    }
  },
  methods: {
    checkGps(){
      if(this.$store.state.location=='OFFICE'){
        if(this.isInside){
          this.$router.push('/absen/selfie')
        }else{
          sdb.alert('Lokasi anda diluar wilayah absen !')
        }
      }else{
        this.$router.push('/absen/selfie')
      }
    },
    refreshGPS(){
      if (typeof cordova == "object") {
        document.addEventListener("deviceready", function() {
            var onSuccess = function(position) {
              alert('Latitude: '          + position.coords.latitude          + '\n' +
                    'Longitude: '         + position.coords.longitude         + '\n' +
                    'Altitude: '          + position.coords.altitude          + '\n' +
                    'Accuracy: '          + position.coords.accuracy          + '\n' +
                    'Altitude Accuracy: ' + position.coords.altitudeAccuracy  + '\n' +
                    'Heading: '           + position.coords.heading           + '\n' +
                    'Speed: '             + position.coords.speed             + '\n' +
                    'Timestamp: '         + position.timestamp                + '\n');
            };

          // onError Callback receives a PositionError object
          //
          function onError(error) {
              alert('code: '    + error.code    + '\n' +
                    'message: ' + error.message + '\n');
          }

            navigator.geolocation.getCurrentPosition(onSuccess, onError);
          });
      }
    },
    loadMap(){
      let that=this;
      console.log(window)
      // var center = JSON.parse(this.$route.query.gps); // 
      maps = new L.Map('mapid');
      maps.setView([this.$store.state.employee.lat,this.$store.state.employee.lng], 17);
      // maps.setView([center.lat,center.long], 18);
      L.tileLayer('https://{s}.google.com/vt/lyrs=m&x={x}&y={y}&z={z}',{
          maxZoom: 20,
          subdomains:['mt0','mt1','mt2','mt3']
      }).addTo(maps);
      //  L.circle(, 100).addTo(maps);
       var circle = L.circle([this.$store.state.employee.lat,this.$store.state.employee.lng], {
      //  var circle = L.circle([center.lat,center.long], {
          color: 'red',
          fillColor: '#f03',
          fillOpacity: 0.5,
          radius: 100
      }).addTo(maps);
      if ("geolocation" in navigator) {
            console.log('geolocation',navigator)
            navigator.geolocation.getCurrentPosition(function (location) {
                appendLocation(location, "fetched");
            });
            watchId = navigator.geolocation.watchPosition(appendLocation);
        } else {
            sdb.alert("Geolocation API not supported.");
        }
      function appendLocation(position, verb) {
        console.log('position',position)
        var latitude = position.coords.latitude;
        var longitude = position.coords.longitude;
        var accuracy = position.coords.accuracy;
        console.log('lat',latitude)
        console.log('long',longitude)
        that.lat=latitude
        that.long=longitude
        that.$store.state.lat=latitude
        that.$store.state.lng=longitude
        var user = new L.marker([latitude,longitude],{
        // var user = new L.marker([-3.447172, 114.771320],{
            draggable: true,
            autoPan: true
        }).addTo(maps);
        var d = maps.distance(user._latlng, circle.getLatLng());
        var isInside = d < circle.getRadius();
        that.isInside=isInside;
        circle.setStyle({
            fillColor: isInside ? 'green' : '#f03'
        })
        that.$forceUpdate();
      }
        
    }
  },
  mounted() {
    setTimeout(() => {
      sdb.alert('Tunggu untuk mendapatkan Pin lokasi anda!','bg-green-400')
    }, 1000);
    this.loadMap();
    // this.users=JSON.parse(localStorage.getItem('absenStatus'))
    this.$forceUpdate();
    // if(localStorage.getItem('users')){
    // }else{
    //   this.$router.push('/')
    // }
  }
};
</script>

